body {
    font-family: 'PT Sans Narrow', sans-serif;
    overflow: hidden;
    white-space: nowrap;
    font-weight:700;
}



#background {
    -webkit-box-shadow: 0px 3px 8px 3px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 3px 8px 3px rgba(0, 0, 0, 0.4);
}

#one, #two, #three, #circle{
	cursor:pointer;
	text-align:center;
}

#circle{
	background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #2C8B42), color-stop(50%, #206030));
	background: -webkit-linear-gradient(top, #2C8B42 50%, #206030 50%);
	background: -o-linear-gradient(top, #2C8B42 50%, #206030 50%);
	background: linear-gradient(180deg, #2C8B42 50%, #206030 50%);
	border:3px solid black;
}
  



